import Logger from "../Utils/LoggerByDefault";
import BaseObject from "ol/Object";

var logger = Logger.getLogger("searchengine");

/**
 * @classdesc
 * Service de recherche abstrait : base commune pour les services d'autocompltion et de gocodage.
 *  surcharger pour chaque type de service (IGN, INSEE, etc.).
 *
 * @alias ol.service.AbstractSearchService
 * @abstract
 * @module SearchService
 */
class AbstractSearchService extends BaseObject {

    /**
     * Constructeur du service abstrait.
     * @constructor
     * @param {AbstractSearchServiceOptions} options Options du service
     */
    constructor (options) {
        options = options || {};

        // call ol.control.Control constructor
        super(options);

        if ((this.constructor == AbstractSearchService)) {
            throw new TypeError("AbstractSearchService cannot be instantiate");
        }
        /**
         * Nom de la classe (heritage)
         * @private
         */
        this.CLASSNAME = "AbstractSearchService";

        // initialisation du composant
        this.initialize(options);

        return this;
    }

    /**
     * Initialise le service avec les options fournies.
     * @protected
     * @param {AbstractSearchServiceOptions} options Options de configuration du service
     */
    initialize (options) {
        this.AUTOCOMPLETE_EVENT = "autocomplete";
        this.SEARCH_EVENT = "search";
        this.ERROR_EVENT = "error";

        this._autocompleteLocations = [];
        this._locations = [];

        if (options.autocomplete === false) {
            this.set("autocomplete", false);
        }
        this.set("index", options.index || "address,poi");
        this.set("limit", typeof options.limit === "number" ? options.limit : 1);
        this.set("returnTrueGeometry", !!options.returnTrueGeometry);
    }

    /**
     * Rcupre la liste des rsultats d'autocompltion.
     * @param {Number} [index] Index du rsultat (optionnel). Si non fourni, retourne tous les rsultats.
     * @returns {Array<AutocompleteResult>|AutocompleteResult} Un tableau de rsultats ou un rsultat unique
     */
    getAutocompleteLocations (index) {
        if (index === undefined) {
            return this._autocompleteLocations;
        } else {
            return this._autocompleteLocations[index];
        }
    }

    /**
     * Rcupre la liste des rsultats de recherche finale.
     * @param {Number} [index] Index du rsultat (optionnel). Si non fourni, retourne tous les rsultats.
     * @returns {Array<SearchResult>|SearchResult} Un tableau de rsultats ou un rsultat unique
     */
    getResult (index) {
        if (index === undefined) {
            return this._locations;
        } else {
            return this._locations[index];
        }
    }

    /**
     * Lance une autocompltion.
     * Mthode  surcharger dans les services concrets.
     * @param {String} text Texte d'autocompltion
     * @abstract
     */
    autocomplete (text) { }


    /**
     * Lance une recherche.
     * Mthode  surcharger dans les services concrets.
     * @param {Object} obj Options de recherche (dpend du service)
     * @abstract
     */
    search (obj) { }


    /**
     * Retourne le titre  afficher pour un rsultat.
     * Mthode  surcharger dans les services concrets.
     * @param {AutocompleteResult} obj Objet dont le titre drive
     * @abstract
     * @returns {String} Titre du rsultat
     */
    getItemTitle (obj) { 
        return obj;
    }

}

export default AbstractSearchService;

// Expose SearchEngine as ol.control.SearchEngine (for a build bundle)
if (window.ol) {
    if (!window.ol.service) {
        window.ol.service = {};
    }
    window.ol.service.AbstractSearchService = AbstractSearchService;
}
